

.centerFormDiv{
    display: flex;
    justify-content: center;
    
}

.quoteForm{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 8px;
    margin-top: 5vh;
    min-width: 500px;
    width: 42vw;
}

.formDivInputHelper{
     margin-top: 10px;
    width: 80%;
    min-width: 468px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.formDivInputHelper>*{
    margin-top: 2px;
    margin-bottom: 2px;
    
}

.labelStyle{
    color:black;
    font-weight: 500;
    font-size: 16px;
}

.inputStyle{
    background-color: rgb(234, 242, 247);
    color:black;
    font-weight: 400;
    font-size: 24px;
    width: 100%;
    box-sizing: border-box; 
}

.inputStyleWarning{
    background-color: rgb(245, 243, 233);
    color:black;
    font-weight: 400;
    font-size: 24px;
    width: 100%;
    box-sizing: border-box; 
}

.formDivButtonHelper{
    margin: 0;
    width: 80%;
    min-width: 468px;
    display: flex;
    justify-content: flex-end;
}

.buttonStyle{
    margin-top: 6px;
    margin-bottom: 12px;
    width: 160px;
    height: 40px;
    transition: transform 0.05s ease;

}

.buttonStyle:hover{
    cursor: pointer;
    transform: scale(1.04)
}